.RightSide{
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.navIcons{
    display: flex;
    justify-content: space-between;
    margin-top: 1rem;
}

.navIcons>img{
    height: 1.5rem;
    width: 1.5rem;
    cursor: pointer;
}

.navIcons>svg{
    cursor: pointer;
}

.rs-btn{
    width: 80%;
    height: 3rem;
    align-self: center;
}

@media(max-width:1093px){
    .RightSide{
        display: none;
    }
}